@import "../../styles/variables.scss";

.comics {
  &__cards {
    margin-top: 24px;
    display: grid;
    grid-template-columns: repeat(auto-fit, 290px);
    justify-content: center;
    row-gap: 40px;
    column-gap: 16px;
  }

  .header__search {
    display: none;
    width: 100%;
    margin-top: 16px;
    border: 1px solid $secondary;

    @media (max-width: 992px) {
      display: flex;
    }
  }

  &__empty {
    margin-top: 30px;
    font-size: 30px;
    color: $red;
  }
}
