@import "./variables.scss";
@import "./reset.scss";
@import "../components/header/Header.scss";
@import "../features/comics/ComicsList.scss";

* {
  font-family: "Montserrat", -apple-system, sans-serif;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $primary-color;
  font-size: 14px;
  background-color: #f0f0f0;
}

.App {
  max-width: 1440px;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.main {
  padding: 0 100px 80px;
  flex-grow: 1;

  @media (max-width: 1024px) {
    padding: 0 30px 50px;
  }

  @media (max-width: 568px) {
    padding: 0 15px 50px;
  }
}

.section-title {
  margin-top: 40px;
  font-weight: 700;
  font-size: 32px;
  line-height: 41px;
}

.section-info {
  margin: 20px auto;
  max-width: 420px;
  text-align: center;
}

.section-counter {
  margin-top: 8px;
  color: $secondary;
}
